*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root{
    --mainColor: crimson;
    --whiteColor: white;
    --blackColor: black;
    --lightDark: #222;
}

body{
    font-family: 'Lato' , Sans-serif;
}

/* Navbar Styling */

nav{
    width: 100%;
    height: 70px;
    line-height: 70px;
    padding: 0 100px;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.5);
    display: flex;
    position: fixed;
    top: 0;
    background: var(--whiteColor);
    z-index: 9999;
}

.left{
    width: 50%;
    text-align: left;
}

.left a{
    font-weight: 700;
    color: var(--mainColor);
    font-size: 25px;
}

.right{
    width: 50%;
    text-align: right;
}

nav ul{
    list-style: none;
}

nav ul a{
    text-decoration: none;
}

.shopping-cart{
    display: inline-block;
    font-size: 30px;
    color: var(--lightDark);
}

.cart-count{
    display: inline-block;
    width: 20px;
    height: 20px;
    background: var(--mainColor);
    color: var(--whiteColor);
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    font-size: 10px;
    position: absolute;
    margin-top: 10px;
}

/* Comman Styling*/

.container{
    width: 100%;
    padding: 0 100px;
}

/* Banner Styling */

header{
    width: 100%;
    background: url("./assets/bbb.jpg");
    height: 500px;
    margin-top: 70px;
    background-repeat: no-repeat;
    background-size: cover;
    color: var(--whiteColor);
}

.header-text{
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-text h1{
    font-size: 50px;
}

.header-text p{
    margin-top: 8px;
    font-size: 19px;
    text-align: center;
}

/* Products Styling */

.products{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.product{
    margin-top: 10px;
    width: 25%;
    padding: 0 12px;
    margin: 20 0;
    overflow: hidden;
    position: relative;
    margin-bottom: 10px;
}

.hot{
    position: absolute;
    top: 0;
    background: red;
    padding: 5px 20px;
    color: var(--whiteColor);
}

.new{
    position: absolute;
    top: 0;
    background: green;
    padding: 5px 20px;
    color: var(--whiteColor);
}

.product-image{
    width: 100%;
    height: 350px;
    overflow: hidden;
}

.product-details{
    padding: 15px 10px;
    text-align: center;
    background: var(--whiteColor);
    border-left: 1px solid #fafafa;
    border-right: 1px solid #fafafa;
}

.product-name{
    font-size: 18px;
    font-weight: 500;
    margin: 12px 0;
}

.product-price{
    font-size: 20px;
    font-weight: 300;
    margin: 12px 0;
}

.add-to-cart{
    background: var(--mainColor);
    padding: 10px 20px;
    width: 100%;
    color: var(--whiteColor);
    text-align: center;
    font-size: 14px;
    cursor: pointer;
}

.add-to-cart:hover{
  background: var(--lightDark);
}

/* Not Found Styling */

.not-found{
    margin-top: 100px;
}

/* Cart Styling */

.cart-container{
    width: 100%;
    margin-top: 40px;
    padding: 0 100px;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
}

.cart-details{
    width: 75%;
    border: 1px solid #fafafa;
    border-radius: 5px;
}

.cart{
    display: flex;
    align-items: center;
    background : var(--whiteColor);
    padding: 15px 20px;
}

.cart-image{
    display: inline-block;
    width: 80px;
    height: 80px;
    border-radius: 5px;
    overflow: hidden;
}

.cart-image img{
    width: 100%;
    height: 100%;
}

.cart-product-name{
    display: inline-block;
    width: 150px;
    padding: 0 15px;
    font-weight: bold;
    font-size: 14px;
}

.cart-product-price{
    display: inline-block;
    width: 120px;
    padding: 0 15px;
    text-align: center;
    font-weight: bold;
    color: var(--mainColor);
}

.inc,
.dec{
    display: inline-block;
    height: 40px;
    width: 40px;
    line-height: 40px;
    text-align: center;
    border: 1px solid var(--mainColor);
    font-size: 14px;
    color: var(--mainColor);
    border-radius: 3px;
    cursor: pointer;
}

.product-quantity{
    display: inline-block;
    width: 70px;
    font-size: 14px;
    padding: 0 15px;
    text-align: center;
}

.product-total-price{
    display: inline-block;
    width: 170px;
    padding: 0 15px;
    text-align: center;
}

.delete-product{
    border:none;
    color: var(--mainColor);
    cursor: pointer;
}

/* Cart Summary Styling */

.cart-summary {
  width: 25%;
  padding: 0 0 0 20px;
  margin-top: 100px;
}

.summary {
  background: var(--whiteColor);
  padding: 13px;
}

.summary h3 {
  font-size: 18px;
  font-weight: normal;
  border-bottom: 1px solid #999;
  padding-bottom: 7px;
}
.total-items {
  display: flex;
  margin: 15px 0;
}
.items {
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.items-count {
  width: 50%;
  text-align: right;
  font-weight: bold;
  color: var(--mainColor);
}
.total-price-section {
  display: flex;
  margin: 15px 0;
}
.just-title {
  width: 50%;
  text-align: left;
  font-weight: normal;
}
.items-price {
  font-weight: bold;
  width: 50%;
  text-align: right;
  color: var(--mainColor);
}
.strip-section {
  margin: 15px 0;
  border-top: 1px solid #999;
  padding-top: 15px;
}

@media(max-width: 500px){

  .left a {
    font-size: 15px;;
  }
  .fa, .fas {
    font-weight: 600;
  }
  header{
    width: auto;
    height: 250px;
  }
  .header-text h1 {
    font-size: 20px;
  }
  .header-text p {
    font-size: 10px;
  }
  .product {
    width: auto;
    overflow:unset;
  }
  .cartContainer {
    flex-direction: column;
  }
  .cartDetails {
    width: 100%;
  }
  .cartSummary {
    width: 100%;
    margin-top: 20px;
  }
  .cart {
    flex-direction: column;
  }
  
  .cartProductName {
    width: 250px;
    padding: 14px 15px;
    font-size: 14px;
    text-align: center;
  }
  .cartProductPrice {
    width: 120px;
    padding: 14 15px;
    font-weight: bold;
    text-align: center;
  }
  .inc {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: 1px solid #222;
    border-radius: 50%;
    cursor: pointer;
    margin: 15px 0;
  }
  .productQuantity {
    display: inline-block;
    padding: 14 15px;
    font-size: 16px;
    width: 70px;
    text-align: center;
  }
  .dec {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    border: 1px solid #222;
    border-radius: 50%;
    cursor: pointer;
    margin: 15px 0;
  }
  .productTotalPrice {
    display: inline-block;
    width: 150px;
    padding: 14px 15px;
    font-weight: bold;
    color: #999;
  }
}